<template>
    <header>
		<nav class="barrasuperior">
			<img @click="chamaWhats" @mouseover="mostrarTexto" @mouseout="ocultarTexto" class="rs_img" :src="rs_img" alt="rs_img">
			<div :class="w_phone">(11) 9 9144 - 2388</div>
		</nav>
    </header>
</template>

<script>
export default {
	data() {
		return {
			rs_img:'./imagens/whatsapp.png',
			w_phone:'w_phone_hidden'
		}
	},
	methods: {
		mostrarTexto(){
			this.w_phone = 'w_phone'
		},
		ocultarTexto(){
			this.w_phone = 'w_phone_hidden'
		},
		chamaWhats(){
			const numeroTelefone = '5511991442388';
			const mensagem = 'Olá, tudo bem?';
			const url = `https://wa.me/${numeroTelefone}?text=${encodeURIComponent(mensagem)}`;
			window.open(url, '_blank');
		}
	},
}
</script>

<style scoped>
.barrasuperior{
	height: 40px;
	background: linear-gradient(0deg,rgb(20, 13, 27) 0%,rgb(23, 15, 32) 15%, rgb(29, 19, 39) 45%, rgb(32, 21, 44) 55%,rgb(27, 18, 37) 78%, rgb(20, 13, 27) 100%);
	padding: 8px;
}

.rs_img{
	margin-top: 3px;
	margin-left: 3px;
	height: 80%;
	cursor: pointer;
}

.w_phone{
	position: absolute;
	top: 10px;
	width: 230px;
	left: calc(50% - 115px);
	color: rgb(59, 202, 114);
	font-weight: 800;
	font-size: 26px;
	transition: 0.5s;
}
.w_phone_hidden{
	position: absolute;
	top: 10px;
	width: 230px;
	left: calc(50% - 115px);	
	color: rgba(59, 202, 114, 0);
	font-weight: 800;
	font-size: 26px;
