<template>
    <div v-if="eMessageView" class="ErrorMessage">
        <div class="MessageWindow">
            <div class="eMessage">{{ eMessage }}</div>
            <div @click="fechaEMessage()" class="btPedido btError">OK</div>
        </div>
    </div>

    <div v-if="pMessageView" class="ErrorMessage">
        <div class="confirmationWindow">
            <div class="msgTop">
                <div class="messageOK">
                    Seu pedido foi realizado com sucesso!
                </div>
                <div class="numPedido">
                    O número do seu pedido é: {{ cPID }}
                </div>
            </div>

            <div class="dadosPedido">
                <div class="dadosClie">
                    <div class="dadosClieDet">
                        ID: {{ cID }}
                    </div>
                    <div class="dadosClieDet">
                        Nome: {{ cNome }}
                    </div>
                    <div class="dadosClieDet">
                        Telefone: {{ cTel }}
                    </div>
                    <div class="dadosClieDet">
                        Email: {{ cEmail }}
                    </div>
                    <div class="dadosClieDet">
                        Endereço: {{ cEndereco }}
                    </div>
                </div>
                <div class="dadosClie pedidoDetalhes">
                    <div class="pedidoDet" v-for="item in pedidoFiltrado" :key="item.id">
                        <div class="pedidoDetItem">Item: {{ item.nome }}</div>
                        <div class="pedidoDetItem">Valor: R$ {{ item.preco.replace('.', ',') }}</div>
                        <div class="pedidoDetItem">Quantidade: {{ item.quantidade }}</div>
                        <div class="pedidoDetItem">Sub-Total: R$ {{ item.precoTotal.toFixed(2).replace('.', ',') }}</div>

                    </div>
                </div>
                <div class="pedidoTotal">
                    Total: <label class="TotalItem">R$ {{ totalcomanda.toFixed(2).replace('.', ',') }}</label>
                </div>
                <div @click="fechaPMessage()" class="btPedido btFinaliza">OK</div>
            </div>
        </div>
    </div>

    <div v-if="totalcomanda" class="soeuabextado">

        <div class="cardTitle">pedido</div>

        <div class="cardCategoria" v-for="categoria in comandaitens" :key="categoria.id">

            <h3 v-if="categoria.subTotal" class="itemName">{{ categoria.nome }}</h3>

            <div v-if="categoria.subTotal" v-for="(ingrediente, index) in categoria.items" :key="index">

                <div v-if="ingrediente.precoTotal" class="cardCategoriaItens">

                    <div class="ingName">
                        <div class="ingDesc">{{ ingrediente.nome }}</div>
                    </div>
                    <div class="ingSum">
                        <div class="ingDet">
                            Preço: <div class="ingPreco">{{ parseFloat(ingrediente.preco).toFixed(2).replace('.', ',') }}
                            </div>
                        </div>
                        <div class="ingQuant">
                            Quantidade: <div class="quantValue">{{ ingrediente.quantidade }}</div>
                        </div>

                    </div>
                    <div class="ingPTot">
                        Preço Total: <div class="quantTValue">{{
                            parseFloat(ingrediente.precoTotal).toFixed(2).replace('.', ',') }}</div>
                    </div>

                </div>

            </div>

        </div>

        <div class="cardCategoriaItens cTotal">

            <div class="ingName nTotal">
                Total:
            </div>
            <div class="quantTValue pTotal">
                R$ {{ ' ' + parseFloat(totalcomanda).toFixed(2).replace('.', ',') }}
            </div>

        </div>

        <div class="cardTitle">observações</div>
        <div class="dadosClie">
            <div class="inpusData">
                <div>Nome:</div>
                <input type="text" class="dadaClie" v-model="cNome" required="">
            </div>
            <div class="inpusData">
                <div>Telefone:</div>
                <input type="text" class="dadaClie" v-model="cTel" required="">
            </div>
            <div class="inpusData">
                <div>Email:</div>
                <input type="text" class="dadaClie" v-model="cEmail">
            </div>
            <div class="inpusData">
                <div>Endereço:</div>
                <textarea v-model="cEndereco" class="observacoes dadaClie"></textarea>
            </div>

        </div>
        <div class="dadosClie">
            <div class="inpusData">
                <div class="obsData">Observação:</div>
                <textarea v-model="cObs" class="observacoes"></textarea>
            </div>
        </div>

        <div @click="enviapedido()" class="btPedido">Finalizar</div>
    </div>
</template>

<script>

import axios from 'axios'

const http = axios.create()

export default {
    props: ["comandaitens", "totalcomanda"],
    data() {
        return {
            eMessageView: false,
            pMessageView: false,
            cID: null,
            cPID: null,
            cObs: null,
            cNome: null,
            cTel: null,
            cEmail: null,
            cEndereco: null,
            BASE_URL: null,
            cLocal: null,
            eMessage: "Dados necessários não informados.",
            pedidoFiltrado: null
        }
    },
    mounted() {
        this.BASE_URL = "https://vizinhoapi.herokuapp.com/"
        // this.cObs = 'Sem cebola por favor'
        // this.cNome = "Rogério"
        // this.cTel = "(11)958478574)"
        // this.cEmail = 'sportsradicais2@getMaxListeners.com'
        // this.cEndereco = 'Rua Pau Grande de Oliveira n 18'
    },
    methods: {
        fechaEMessage() {
            this.eMessageView = false
        },
        showEMessage() {
            this.eMessageView = true
        },
        fechaPMessage() {
            window.location.reload();
            this.pMessageView = false
        },
        showPMessage() {
            this.pMessageView = true
        },
        enviapedido() {

            if (!!this.cTel) {
                this.cTel = this.cTel.replace(/[.,() -]/g, '')
                this.cTel = this.cTel.replace(/\D/g, '');
            }

            if (!this.cNome || !this.cTel) {
                this.showEMessage()
            } else {

                this.cLocal = this.comandaitens

                this.cLocal.map(item => {

                    item.item_img = null

                })

                var cFiltrado = []

                this.cLocal.forEach(item => {
                    const itemsFiltrados = item.items.filter(item2 => {
                        return item2.quantidade > 0
                    })

                    cFiltrado.push(...itemsFiltrados)
                })

                // console.log(cFiltrado)

                this.pedidoFiltrado = cFiltrado

                let params = {
                    nome: this.cNome,
                    email: this.cEmail,
                    tel: this.cTel,
                    endereco: this.cEndereco,
                    total: this.totalcomanda,
                    obs: this.cObs,
                    pedido: cFiltrado
                }

                // console.log(params)

                http.post(this.BASE_URL + 'pedidos',

                    {
                        params
                    }

                ).then((result) => {

                    let rPedido = result.data.response
                    // console.log(rPedido)
                    this.cID = rPedido.cliente.idcliente
                    this.cPID = rPedido.pedido_id
                    this.showPMessage()

                }, (ct_error) => {

                    console.log({ error: ct_error })

                })

            }
        }
    },
}
</script>

<style scoped>
textarea:focus,
input:focus {
    border: none;
    outline: none;
    resize: none;
}

textarea {
    border: none;
    outline: none;
    resize: none;
}

.soeuabextado {
    background-color: rgb(226, 226, 226);
    width: 515px;
    min-width: 515px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 15px;
    padding: 30px;
    box-shadow: 3px 3px 4px #000000;
}

.cardCategoria {
    margin-bottom: 50px;
}

.cardCategoriaItens {
    background-color: rgb(209, 237, 255);
    padding: 15px;
    margin-top: 15px;
    border-radius: 10px;
    box-shadow: 3px 3px 4px #5c5c5c;
}

.itemName {}

.ingName {
    font-weight: 700;
    margin-bottom: 15px;
}

.ingSum {
    display: flex;
}

.ingDet {
    font-weight: 700;
    display: flex;
    width: 120px;
}

.ingPreco {
    margin-left: 20px;
}

.ingQuant,
.ingPTot {
    font-weight: 700;
    display: flex;
}

.quantValue {
    font-weight: 500;
    margin-left: 20px;
}

.quantTValue {
    font-weight: 700;
    margin-left: 20px;
    color: rgb(29, 29, 168);
    text-shadow: 1px 1px #0000005e;
}

.cardCategoriaItensVazioslinha {
    display: flex;
}

.cardTitle {
    text-align: center;
    font-weight: 700;
    font-size: 24px;
    text-transform: uppercase;
    margin: 30px;
    margin-top: 0px;
}

.observacoes {
    width: 92%;
    height: 220px;
    border-radius: 10px;
    padding: 20px;
    margin-left: auto;
    margin-right: auto;
}

.cTotal {
    height: 100px;
    padding: 0;
    margin-bottom: 30px;
}

.pTotal {
    font-size: 36px;
    margin: 0;
    margin-top: -30px;
    width: 100%;
    text-align: center;
}

.nTotal {
    padding: 10px 20px;
}

.dadaClie {
    width: calc(100% - 24px);
    font-size: 28px;
    border-radius: 6px;
    margin-bottom: 12px;
    margin-top: 6px;
    padding: 12px;
}

.dadosClie {
    padding: 20px;
    margin-bottom: 30px;
    border: 1px solid #00000027;
    font-weight: 700;
    border-radius: 10px;
    box-shadow: 2px 2px 4px #00000027;
    ;
}

.btPedido {
    background-color: rgb(232, 250, 255);
    width: 180px;
    height: 40px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 30px;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 3px 3px 4px #888888;
    cursor: pointer;
    border-radius: 5px;
    font-weight: 600;
    transition: 0.2s;
}

.btPedido:hover {
    background-color: rgb(95, 171, 201);
    color: aliceblue;
}

.obsData {
    margin-bottom: 18px;
}

.ErrorMessage {
    position: absolute;
    width: 100%;
