    color: rgb(0, 0, 117);
}

.TotalItem {
    font-weight: 900;
    color: rgb(0, 65, 22);
}

.btFinaliza {
    background-color: rgb(36, 53, 105);
    color: aliceblue;
}

.btFinaliza:hover {
    background-color: rgb(221, 230, 255);
    color: rgb(4, 5, 46);
    font-weight: 800;
}
</style>