<template>
  <div class="cardTitle">Cardápio</div>

  <section id="panelCardapio">

    <div class="cardCategoria" v-for="categoria in cardapio" :key="categoria.id">
      <div class="cardFotoContainer">
        <img class="cardFoto" :src="categoria.item_img" alt="image_item" />
      </div>
      <h3 class="itemName">{{ categoria.nome }}</h3>

      <div class="btMostrarItens" @click="mostrarItens(categoria.id)">Adicionar</div>

      <div v-if="categoria.item_view" class="ingAdd">
        <div class="ingAddRoll">
          <div class="ingitem" v-for="(ingrediente, index) in categoria.items" :key="index">
            <div class="ingName">
              <div class="ingIndice">{{ index + 1 }}.</div>
              <div class="ingDesc">{{ ingrediente.nome }}</div>
            </div>
            <div class="ingSum">
              <div class="ingDet">
                Preço:
                <div class="ingPreco">
                  {{ parseFloat(ingrediente.preco).toFixed(2).replace(".", ",") }}
                </div>
              </div>
              <div class="ingQuant">
                <div class="decQuant" @click="decrementar(ingrediente)">-</div>
                <div class="quantValue">{{ ingrediente.quantidade }}</div>
                <div class="sumQuant" @click="incrementar(ingrediente)">+</div>
              </div>
            </div>
          </div>
        </div>

        <div @click="enviapedido(), ocultarItens(categoria.id)" class="btPedido">OK</div>

      </div>
    </div>
  </section>

  <!-- <div @click="enviapedido()" class="btPedido">Seu Pedido</div> -->
</template>

<script>

import axios from 'axios'

const http = axios.create()

export default {
  data() {
    return {
      itensTot: 0,
      BASE_URL: null,
      cardapio: []
    };
  },
  mounted() {
    // this.BASE_URL = process.env.VUE_APP_BASE_URL
    this.BASE_URL = "https://vizinhoapi.herokuapp.com/"
    // console.log(this.BASE_URL)
    this.obterCardapio()
  },
  methods: {
    async obterCardapio() {

      await http.get(this.BASE_URL + 'itens', {}).then((res) => {

        let tCardapio = res.data.response.itens.map(card => {
          return card
        })

        tCardapio.map(item =>{
          item.item_view = false
          item.item_img = "data:image/jpeg;base64," + item.item_img
          item.subtotal = 0
          item.items.map(item2=>{
            item2.quantidade = 0
            item2.precoTotal = 0
          })
        })       

        // console.log(tCardapio)

        this.cardapio = tCardapio

      }, (err) => {
        console.log(err)
      })
    },
    formatarValor(event) {
      // Remove tudo que não é número
      let valor = event.target.value.replace(/\D/g, "");
      // Formata o valor com separador de milhar e duas casas decimais
      let valorFormatado = parseFloat(valor).toLocaleString("pt-BR", {
        minimumFractionDigits: 2,
      });
      // Atribui o valor formatado à propriedade preco do ingrediente
      this.ingrediente.preco = valorFormatado;
    },
    incrementar(ingrediente) {
      ingrediente.quantidade++;
      ingrediente.precoTotal = ingrediente.preco * ingrediente.quantidade;
      this.calcularPrecoTotal();
      this.calcularSubTotal();
      this.enviapedido();
    },
    decrementar(ingrediente) {
      if (ingrediente.quantidade > 0) {
        ingrediente.quantidade--;
        ingrediente.precoTotal = ingrediente.preco * ingrediente.quantidade;
      }
      this.calcularPrecoTotal();
      this.calcularSubTotal();
      this.enviapedido();
    },
    calcularPrecoTotal() {
      let somaPrecos = 0;

      for (let i = 0; i < this.cardapio.length; i++) {
        for (let j = 0; j < this.cardapio[i].items.length; j++) {
          if (this.cardapio[i].items[j].precoTotal > 0) {
            somaPrecos += this.cardapio[i].items[j].precoTotal;
          }
        }
      }

      this.itensTot = somaPrecos;
    },
    calcularSubTotal() {
      for (let i = 0; i < this.cardapio.length; i++) {
        let subTotal = 0;
        for (let j = 0; j < this.cardapio[i].items.length; j++) {
          subTotal += this.cardapio[i].items[j].precoTotal;
        }
        this.cardapio[i].subTotal = subTotal;
        // console.log(this.cardapio[i].subTotal);
      }
    },
    async fechaTodos() {
      this.cardapio.forEach((item) => {
        item.item_view = false
      })
    },
    async mostrarItens(item_id) {
      var idItem = item_id - 1;

      await this.fechaTodos()

      // console.log(idItem)

      if (this.cardapio[idItem].item_view === false) {
        // console.log("encontrou como false");
        this.cardapio[idItem].item_view = true;
      } else {
        // console.log("encontrou como true");
        this.cardapio[idItem].item_view = false;
      }
    },
    ocultarItens(item_id) {
      var idItem = item_id - 1;
      this.cardapio[idItem].item_view = false;
    },
    enviapedido() {
      const TotComanda = this.itensTot;
      const oItensPedido = this.cardapio;
      // console.log({tc:TotComanda,tp:oItensPedido})
      this.$emit("pedido-fechado", { TotComanda, oItensPedido });
    },
  },
};
</script>

<style scoped>
#panelCardapio {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.cardCategoria {
  width: 230px;
  min-height: 320px;
  background-color: aliceblue;
  box-shadow: 3px 3px 4px #888888;
  padding: 15px;
  border-radius: 10px;
  margin: 10px;
}

.cardTitle {
  text-align: center;
  font-weight: 700;
  font-size: 30px;
  text-transform: uppercase;
  margin: 30px;
  margin-top: 0px;
}

.itemName {
  text-align: center;
  font-weight: 700;
  font-size: 26px;
  text-transform: uppercase;
  margin: 30px;
}

.cardFotoContainer {
  width: 100%;
  height: 0;
  padding-bottom: 65%;
  max-width: 100%;
  box-shadow: 3px 3px 4px #888888;
  border-radius: 10px;
  overflow: hidden;
  margin-bottom: 20px;
}

.cardFoto {
  width: 120%;
  margin-left: -12%;
}

.ingitem {
  padding: 10px;
  background-color: aliceblue;
  margin-bottom: 10px;
  border-radius: 8px;
}

.ingName {
  display: flex;
  width: 100%;
  margin-bottom: 10px;
}

.ingDesc {
  width: 100%;
  text-align: center;
}

.ingDet,
.ingIndice {
  font-weight: 700;
  margin-right: 10px;
}

.ingDet {
  display: flex;
  margin-bottom: 10px;
  margin-top: 5px;
}

.ingPreco {
  font-weight: 400;
  margin-left: 10px;
}

.ingQuant {
  display: flex;
  margin-bottom: 20px;
  margin-left: 20px;
}

.decQuant,
.sumQuant {
  background-color: rgb(232, 207, 255);
  box-shadow: 3px 3px 4px #888888;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  font-weight: 700;
  cursor: pointer;
}

.decQuant:hover,
.sumQuant:hover {
  background-color: rgb(196, 161, 230);
}

.ingSum {
  display: flex;
  height: 40px;
}

.decQuant {
  margin-right: 20px;
}

.sumQuant {
  margin-left: 20px;
}

.quantValue {
  margin-top: 5px;
}

.btMostrarItens {
  background-color: rgb(195, 251, 253);
  height: 40px;
  width: 70%;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 3px 3px 4px #888888;
  margin-bottom: 30px;
  border-radius: 5px;
  font-weight: 700;
  cursor: pointer;
}

.btMostrarItens:hover {
  background-color: rgb(155, 222, 224);
}

.btPedido {
  background-color: rgb(232, 250, 255);
  width: 180px;
  height: 40px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 3px 3px 4px #888888;
  cursor: pointer;
  border-radius: 5px;
  font-weight: 600;
  transition: 0.2s;
}

.btPedido:hover {
  background-color: rgb(95, 171, 201);
  color: aliceblue;
}

.ingAdd {
  position: absolute;
  width: 400px;
  height: 800px;
  top: 15%;
  left: calc(50% - 150px);
  background-color: rgb(182, 229, 243);
  box-shadow: 3px 3px 4px #888888;
  border-radius: 5px;
  padding-top: 20px;
}

.ingAddRoll {
  padding: 10px;
  overflow: auto;
  height: 83%;
